$dark: #424242;

.TableWrap{
  display: flex;
  flex-direction: column;
  align-items: center;
  .TableContainer{
    overflow-y: scroll;
    height: 80vh
  }

  .Pagination{

    max-width: 600px;
    margin-top: 30px;
    box-sizing: border-box;

    .PaginationButton{
      background-color: $dark;

      margin-bottom: 10px;
      width: 35px;
      height: 35px;
      color: white;


      &:not(:first-child), &:not(:last-child){

      }

      &:nth-last-child(2){
        border-bottom-right-radius: 50%;
        border-top-right-radius: 50%;
      }
      &:nth-child(2){
        border-bottom-left-radius: 50%;
        border-top-left-radius: 50%;
      }

      &:first-child, &:last-child{
        border-radius: 50%;
      }
      &:first-child{
        margin-right: 10px;
      }
      &:last-child{
        margin-left: 10px;
      }


      &.Active{
        background-color: #b44242;
      }
    }

  }
}



.Table {
  border-spacing: 0;
  background-color: $dark;
  color: white;
  border-radius: 10px;
  padding: 20px;
  min-height: 600px;
  min-width: 600px;
  height: 600px;

  thead{
    tr{
      color: white;
      th {
        font-size: 1.1em;
        padding: 10px 5px;
      }
    }
  }

  tr {
    border-spacing: 0;
    box-shadow: 0 1px 0 0 #656565;
  }

  td {

    border-collapse: collapse;
    min-width: 100px;

    padding: 15px;
    &:not(:first-child){
      text-align: right;
    }
  }
}



